import React from "react";

import { Helmet } from "react-helmet";

import "./styles.css";
import { Body } from "../common/body";
import moment from "moment";
import 'moment/locale/ru';
import { graphql } from "gatsby";
import { CalendarQuery, QueryData } from "../common/lib/model";

export const query = graphql`
{
  allBrangsJson {
    nodes {
      date
      tags
      name
      id
    }
    totalCount
  }
  allTagsJson {
    nodes {
      name
    }
  }
}
`

const seo = {
  url: "https://t3.gg",
  title: "Minimal Gatsby Typescript Template",
  description: "Minimal Gatsby Typescript Template",
  favicon: "/icon.png"
};

moment.locale('ru', {
  week: {
    dow: 1,
  }
})

const home = ({data}: QueryData<CalendarQuery>) => (
  <div className="home">
    <title>{seo.title}</title>
    <Helmet title={seo.title}>
      <meta name="description" content={seo.description} />
      <link rel="icon" type="image/png" sizes="32x32" href={seo.favicon} />

      <meta property="og:url" content={seo.url} />
      <meta property="og:title" content={seo.title} />
      <meta property="og:description" content={seo.description} />
      <meta property="og:type" content="website" />
    </Helmet>
    <Body brangs={data.allBrangsJson.nodes} tags={data.allTagsJson.nodes.map(tag => tag.name)}/>
  </div>
);

export default home;
